import React from "react";
import Card from "react-bootstrap/Card";
import { ImPointRight } from "react-icons/im";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
            Hi Everyone, I am <span className="purple">Avi Paghadar</span>
            <span className="purple">&nbsp;from Gujarat, India.</span>
            <br />I am pursuing B.tech in Computer Engineering in BIRLA VISHVAKARMA MAHAVIDHYALAYA.
            <br />
            <br />
            Apart from coding, some other activities that I love to do!
          </p>
          <ul>
            <li className="about-activity">
              <ImPointRight /> Playing Cricket
            </li>
            <li className="about-activity">
              <ImPointRight /> Writting More Efficient Code
            </li>
            <li className="about-activity">
              <ImPointRight /> Watching Movies 
            </li>
          </ul>

          <p style={{ marginBlockEnd: 0, color: "rgb(155 126 172)" }}>
            "Strive to build things that make a difference!"{" "}
          </p>
          <footer className="blockquote-footer">Avi Paghadar</footer>
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
